:root {
  --blue: #444f63;
  --shadow: 1px 2px 4px var(--blue);
}

.calendar {
  position: relative;
  max-width: 320px;
  margin: 0 auto;
  padding: 32px 16px 32px 16px;
  border-top-left-radius: 180px;
  border-top-right-radius: 180px;
  margin-top: 16px;  
  min-width: 320px;
  height: auto;
  background-color: var(--blue);
  box-shadow: var(--shadow);
  border: 1px solid var(--blue);
}

.gap {
  width: 35%;
  height: 2000px;
  background-color: white;
  position: absolute;
  left: 33%;
  top: calc(66% - 16px);
  border-top-right-radius: 128px;
}

.calendar img {
  max-width: 100%;
  display: block;
}

.days-bar {
  box-shadow: var(--shadow);
  border: 1px solid var(--blue);
  padding: 32px 0;
  background-color: var(--blue);
  width: 35%;
  text-align: center;
  border-top-right-radius: 128px;
  position: absolute;
  top: calc(66% - 16px);
  left: calc(32% + 1px);
}

.days-bar:hover {
  cursor: pointer;
}

.dow-container, .day-container {
  width: 100%;
  padding-top: 16px;
  text-align: center;
}

.dow-container .day-of-the-week {
  transform: scale(1.4);
}
.day-container {
  padding-top: 32px;
}
.day-container .day {
  transform: scale(2);
}
.days-container {
  width: 32%;
  height: 1130px;
  position: absolute;
  top: 100%;
  background-color: var(--blue);
  padding: 16px 0;
  box-shadow: var(--shadow);
}
.days-container.left {
  left: -1px;
}
.days-container.right {
  right: -1px;
}

.day-of-the-week {
  display: block;
  width: 64px;
  padding-top: 48px;
  margin: 0 auto;
  background-image: url('./assets/napok.png');
  background-size: 100% auto;
  background-repeat: no-repeat;
}
.day-of-the-week.K {
  background-position-y: -44px;
  padding-top: 46px;
  margin-bottom: 6px;
}
.day-of-the-week.SZ {
  background-position-y: -88px;
  padding-top: 43px;
}
.day-of-the-week.CS {
  background-position-y: -131px;
  padding-top: 37px;
  margin-bottom: 2px;
  margin-top: 3px;
}
.day-of-the-week.P {
  background-position-y: -166px;
  margin-bottom: 2px;
  margin-top: 2px;
  padding-top: 43px;
}
.day-of-the-week.V {
  background-position-y: -244px;
  margin-top: 4px;
  padding-top: 38px;
}
.day-of-the-week.H {
  background-position-y: 0;
}

.day {
  margin: 0 auto;
  width: 48px;
  padding-top: 34px;
  background-image: url('./assets/szamok.png');
  background-size: 400% auto;
  background-repeat: no-repeat;
}

.day-2, .day-11, .day-20, .day-29 {
  background-position-y: -34px;
  padding-top: 31px;
}

.day-3, .day-12, .day-21, .day-30 {
  background-position-y: -65px;
  padding-top: 30px;
}

.day-4, .day-13, .day-22, .day-31 {
  background-position-y: -95px;
  padding-top: 29px;
}

.day-5, .day-14, .day-23 {
  background-position-y: -123px;
  padding-top: 32px;
}
.day-14 {
  padding-top: 31px;
}

.day-6, .day-15, .day-24 {
  background-position-y: -153px;
  padding-top: 30px;
}
.day-15 {
  transform: scale(0.9);
}

.day-7 {
  background-position-y: -181px;
  padding-top: 30px;
}
.day-16, .day-25 {
  background-position-y: -183px;
  padding-top: 29px;
}

.day-8, .day-17, .day-26 {
  background-position-y: -209px;
  padding-top: 33px;
}
.day-17 {
  background-position-y: -210px;
}

.day-9, .day-18, .day-27 {
  background-position-y: -241px;
  padding-top: 29px;
}
.day-10, .day-11, .day-12, .day-13, .day-14, .day-15, .day-16, .day-17, .day-18 {
  background-position-x: -42px;
}

.day-19, .day-20, .day-21, .day-22, .day-23, .day-24, .day-25, .day-26, .day-27 {
  background-position-x: -84px;
}

.day-28, .day-29, .day-30, .day-31 {
  background-position-x: -123px;
}

.appear {
  animation: appear 2s ease-in-out;
}

@keyframes appear {
  0% {
    transform: scale(2);
  }
  50% {
    transform: scale(3);
  }
  100% {
    transform: scale(2);
  }
}